@import "mixins";

header {
  display: block;
  position: absolute;
  margin: auto;
  width: 100%;
  padding-top: 40px;
  background-color: transparent;
  z-index: 4; /*overlay:2 - body:0 */
  &.shirk {
    position: fixed;
  }
  p.menu {
    cursor: pointer;
    font-weight: 500;
    font-size: 12px;
    line-height: 30px;
    /* identical to box height, or 250% */
    letter-spacing: 0.03em;
    text-transform: uppercase;
    color: #ffffff;
    position: relative;
    left: -33px;
    z-index: 8;
    transition: 0.4s;
    // &:after{
    //   content: url("../../../assets/images/Ellipse\ 1.png");
    // }
  }
  .circle {
    /*This creates a 1px solid red border around your element(div) */
    border: 2px solid red;
    background-color: transparent;
    height: 40px;
    /* border-radius 50% will make it fully rounded. */
    border-radius: 50%;
    -moz-border-radius: 50%;
    -webkit-border-radius: 50%;
    width: 40px;
    position: absolute;
    top: -0;
    right: 0;
    z-index: 7;
    cursor: pointer;
  }
}
// media queries
.over {
  display: none !important;
}
.over.show {
  display: block !important;
  height: 100vh;
  transition: 0.5s;
  padding: 10rem 5rem;
  overflow: hidden;
  transition: 0.5s;
}

.overlay {
  // background-color: #fff;
  color: #fff;
  height: 0%;
  background-image: url("../images/BG 1.png");
  background-color: #000;
  position: fixed;
  width: 100%;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  overflow-x: hidden;
  transition: 0.5s;
  z-index: 6; /*menu:3 - body:0 */

  .full-height {
    height: 100%;
  }
  .overlay-menu {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-right: 209px;
    ul {
      li.menu-item {
        font-size: 60px;
        line-height: 60px;
        letter-spacing: 0.005em;
        margin-bottom: 25px;
        color: #fff;
        sup {
          top: -3em;
          left: 10px;
          line-height: 0;
        }
      }
    }
  }
}
.overlay-contact {
  display: flex;
  justify-content: center;
  ul {
    display: flex;
    margin-top: 30px;
    li {
      margin-right: 34px;
      cursor: pointer;
    }
  }
  .contact-title {
    font-weight: bold;
    font-size: 18px;
    line-height: 32px;
    letter-spacing: 0.005em;
    color: #ffffff;
  }
  .contact-email {
    font-size: 14px;
    line-height: 32px;
    letter-spacing: 0.005em;
    margin-top: 24px;
    color: #ffffff;
  }
  .contact-desc {
    font-size: 12px;
    line-height: 20px;
    /* or 167% */
    letter-spacing: 0.005em;
    color: #b9b9b9;
    opacity: 0.4;
    margin-top: 7px;
  }
}
@mixin header-lg {
  header {
    // background-color: #8bc34a;
    padding: 12px 15px;
  }
}
