/************************** START ************************/
html {
  font-family: Helvetica, sans-serif;
}
body.hidden {
  overflow-y: hidden; /* Hide vertical scrollbar */
  overflow-x: hidden; /* Hide horizontal scrollbar */
}
$color-first: #fff;
$color-second: #b9b9b9;
span.after-cicle {
  color: #be272d;
  font-size: 170px;
  line-height: 0;
  &--mini {
    font-size: 50px;
    &.mini--sup {
      font-size: 20px;
    }
  }
}
sup {
  top: -1.5em;
  font-weight: bold;
  font-size: 14px;
  line-height: 68px;
  letter-spacing: 0.005em;
  color: #be272d;
}
h4.post-title {
  font-size: 60px;
  line-height: 60px;
  /* or 100% */
  letter-spacing: 0.005em;
  color: rgba(255, 255, 255, 0.3);
  position: relative;
}
small.post-small {
  font-size: 12px;
  line-height: 60px;
  /* identical to box height, or 500% */
  letter-spacing: 0.2em;
  color: rgba(255, 255, 255, 0.3);
}
//custom AOS
[data-aos="example-anim1"] {
  transition-property: transform, opacity,color;
  &.aos-animate {
    color: #fff;
  }
}
#banner {
  background-image: url("../images/banner.png");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  height: 100%;
  padding:5rem 0;
  .banner-text {
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    h2.intro {
      font-size: 90px;
      line-height: 82px;
      letter-spacing: -0.01em;
      color: $color-first;
      position: relative;
    }
    p.sub {
      margin-top: 41px;
      font-size: 18px;
      line-height: 40px;
      letter-spacing: 0.005em;
      color: $color-second;
      font-weight: bold;
      position: relative;
      span {
        height: 2px;
        width: 150px;
        background: #fff;
        position: absolute;
        bottom: 13px;
        left: 150px;
        &:before {
          content: url(../images/Subtract.png);
          position: absolute;
          bottom: -32px;
          right: -21px;
        }
      }
    }
  }
}
#footer {
  background-image: url("../images/BG\ 1.png");
  background-color: #000;
  height: 100vh;
  .footer {
    display: flex;
    flex-direction: column;
    height: 100vh;
    justify-content: center;
    align-items: center;
    // padding: 10rem 0;

    &-click-text {
      font-size: 20px;
      line-height: 40px;
      text-align: center;
      letter-spacing: 0.005em;
      color: #b9b9b9;
    }
    &-large-text {
      font-weight: bold;
      font-size: 120px;
      line-height: 120px;
      letter-spacing: 0.005em;
      color: #ffffff;
      position: relative;
    }
    &-mini-text {
      font-weight: 300;
      font-size: 16px;
      line-height: 28px;
      text-align: center;
      letter-spacing: 0.005em;
      color: #ffffff;
      margin-top: 14px;
      width: 50%;
    }
  }
  .footer-copyright {
    background-image: url("../images/BG\ 1.png");
    background-color: #000;
    padding: 5rem 0;
    .footer-intro {
      justify-content: space-between;
      &__left {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        figure {
          img {
            width: 40%;
          }
        }
        ul {
          display: flex;
          color: #fff;
          li {
            margin-right: 34px;
            cursor: pointer;
          }
        }
      }
      &__right {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        text-align: right;
        .email {
          font-weight: bold;
          font-size: 14px;
          line-height: 32px;
          text-align: right;
          letter-spacing: 0.005em;
          color: #ffffff;
        }
        .copyright {
          font-size: 12px;
          line-height: 20px;
          text-align: right;
          letter-spacing: 0.005em;
          color: #ffffff;
          opacity: 0.4;
        }
      }
    }
  }
}
/************************** OUT PROJECTS   ************************/
.out-text-head {
  font-size: 64px;
  line-height: 68px;
  letter-spacing: 0.005em;
  color: #ffffff;
  width: 770px;
  margin: auto;
  &--out-text-up {
    transform: translateY(-50px) !important;
  }
}
p.text-out-project {
  /* color: red; */
  font-size: 14px;
  line-height: 24px;
  letter-spacing: 0.005em;
  color: #ffffff;
  width: 33%;
  // margin-bottom: 2rem;
  transform: translateY(-105px) !important;
  margin: auto auto 2rem;
  /* margin-top: -53px; */
  &:last-child {
    margin-bottom: 0;
  }
}
.video {
  width: 100%;
  object-fit: cover;
  z-index: 3;
}
.wrapper {
  display: table;
  width: auto;
  position: relative;
  width: 100%;
  z-index: 2;
}
.playpause {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%);
  .fas {
    font-size: 21px;
    color: #fff;
    height: 88px;
    width: 88px;
    border: 2px solid #fff;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
  }
}
li.script-item.scroll-active {
  sup {
    color: #be272d !important;
    opacity: 1 !important;
  }
  h4.post-title,
  small {
    color: #fff;
  }
}
#banner-out-projects {
  background-image: url("../images/BG\ 1.png");
  background-color: #000;
  height: 100%;
  color: #fff;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 4rem 0;
  .container{
    height: 100vh;
  }
}
#beleive {
  background-image: url("../images/image\ 2.png");
  background-size: cover;
  background-position: center;
  height: 100vh;
  background-repeat: no-repeat;
}
#what-we-do {
  background-image: url("../images/BG\ 1.png");
  background-color: #000;
  height: 100%;
  color: #fff;
  padding-bottom: 76px;
  h3.text-out-project{
    transform: translateY(-105px) !important;
  }
  h4.out-text-title {
    width: 34%;
    margin: auto;
    font-weight: 500;
    font-size: 24px;
    line-height: 37px;
    letter-spacing: 0.005em;
    color: #ffffff;
    margin-bottom: 4.5rem;
    
  }
  .list-script {
    margin: auto;
    width: 34%;
    .script-item {
      font-weight: bold;
      font-size: 18px;
      line-height: 68px;
      letter-spacing: 0.005em;
      color: #ffffff;
      &:last-child {
        margin-bottom: 0;
      }
    }

  }
  .list-script-nested {
    transform: translateX(105px);
    .text-out-project {
      &--color-second {
        color: $color-second;
        width: 100%;
        transform: translateY(0) !important;
        margin-bottom: 20px;
      }
      &:last-child {
        // margin-bottom: 0;
      }
    }
  }
}
#image-cover {
  background-image: url("../../assets/images/image 5.png");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  height: 100vh;
}
#moment {
  background-image: url("../images/BG\ 1.png");
  background-color: #000;
  background-size: cover;
  background-position: center;
  height: 100%;
  background-repeat: no-repeat;
  padding-bottom: 128px;
  // padding: 2rem 0;
  .text-container {
    margin: 0;
  }
  .group-image-moment {
    margin-top: 30px;
    &:last-child {
      margin-top: 0;
    }
    figure {
      margin: 15px;
    }
  }
}
#our-showreel {
  background-image: url("../images/BG\ 1.png");
  background-color: #000;
  background-size: cover;
  background-position: center;
  height: 100%;
  background-repeat: no-repeat;
  padding-top: 128px;
  .text-container {
    margin: 0;
    position: absolute;
    z-index: 3;
    transform: translateY(-133px) !important;
  }
}
#about {
  background-image: url("../images/banner.png");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  height: 100%;
  padding:5rem 0;
  overflow-x: hidden;
  .list-script {
    display: flex;
    justify-content: center;
    align-items: center;
    color: #fff;
    flex-direction: column;
    height: 100vh;
    .script-item {
      position: relative;
      margin-bottom: 26px;
      width: 55%;
      a:hover {
        sup {
          color: #be272d;
          opacity: 1;
        }
        h4.post-title,
        small {
          color: #fff;
        }
      }
      &:last-child {
        margin-bottom: 0;
      }
      sup {
        position: absolute;
        left: -42px;
        font-weight: bold;
        font-size: 14px;
        line-height: 60px;
        letter-spacing: 0.005em;
        color: rgba(255, 255, 255, 0.3);
        opacity: 0.3;
      }
    }
  }
}
#showreel{
  background-image: url("../images/BG\ 1.png");
  background-color: #000;
  background-size: cover;
  background-position: center;
  height: 100%;
  background-repeat: no-repeat;
  width: 100%;
  padding: 220px 0;
  // overflow-x: hidden;
  video{
    margin: 80px 0;
  }
  .showreel-script{
    margin: auto;
    display: flex;
    justify-content: space-between;
    margin-bottom: 80px;
    width: 50%;
    &:last-child{
      margin-bottom: 0;
    }
    &__left{
      // margin-right: 71px;
      p{
        font-size: 16px;
        line-height: 32px;
        letter-spacing: 0.005em;
        color: $color-second;
        opacity: 0.6;
      }
    }
    &__right{
      max-width: 468px;
      width: 468px;

      p.text-script{
        font-size: 16px;
        line-height: 32px;
        letter-spacing: 0.005em;
        color: $color-second;
        opacity: 0.6;
        margin-bottom: 20px;
        &:first-child{
          color: #FFFFFF;
          opacity: 1;
        }
        &:last-child{
          margin-bottom: 0;
        }
      }
      &--rev{
        .showreel-credit{
          // width: 468px;
          p.text-script{
            color:$color-first;
            opacity: 0.5;
            margin-bottom: 5px;
            &:last-child{
              margin-bottom: 0;
            }
          }
          &__left{
            margin-right: 28px;
            width: 170px;
          }
          &__right{
            width: 170px;
            p.text-script{
              opacity: 1;
            }
          }
        }
      }
    }

  }
  li.script-item.aos-init.aos-animate a{

  }
}
