#contact{
  background-image:url("../images/banner.png");
  // background-color: ;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  height: 100%;
  z-index: 3;
  color:$color-first;
  padding: 5rem 0;
  .container{
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    z-index: 3;
    h5.contact-text{
      font-weight: bold;
      font-size: 48px;
      line-height: 51px;
      letter-spacing: 0.005em;
      color: #FFFFFF;
      width: 370px;
      margin-bottom: 50px;
      transform: translateX(-45%);
      z-index: 2;
    }
    .contact{
      transform: translateX(-25%);
      z-index: 2;
    }
  }
}

@include for-tablets {
  #banner .banner-text h2.intro {
    font-size: 50px;
    line-height: 70px;
    letter-spacing: -0.01em;
    color: #fff;
    position: relative;
}
  p.text-out-project{
    width: 50%;
  }
  .out-text-head{
    width: 100%;
  }
  #our-showreel .text-container{
    width: 90% ; 
  }
  #moment{
    padding-bottom: 195px;
  }
  #our-showreel {
    padding-top: 0;;
  }
  #what-we-do h4.out-text-title{
    width: 55%;
  }
  #footer{
    height: 100%;
  }
  #footer .footer-large-text{
    font-size: 100px;
    text-align: center;
  }
  #about .list-script .script-item{
    width: 100%;
    text-align: center;
  }
  .showreel-script__left{
    margin-right: 28px;
  }
  .showreel-script__right{
    width: 340px;
  }
}
@include for-phone-only {
  .after-cicle{
    font-size: 100px !important;
    &--mini{
      font-size: 50px !important;
    }
  }
  .out-text-head{
    font-size: 33px;
    line-height: 1.6;
    &--out-text-up{
      transform: translateY(-32px) !important
    }
  }
  p.text-out-project{
    width: 90%;
    transform: translateY(-75px) !important;
    margin: auto;
  }
  h4.post-title{
    font-size: 30px;
    line-height: 30px;
  }
  #what-we-do{
    h4.out-text-title{
      font-size: 18px;
      width: 80%;
      margin-bottom: 2rem;
    }
    .list-script{
      width: 100%;
      font-size: 16px;
      .list-script-nested{
        width: 70%;
        margin: auto;
        transform: translateX(0);
      }
    }
  }
  #moment{
    padding-bottom: 145px;
    .group-image-moment{
      margin-top: 0;
      flex-direction: column;
    }
  }
  #our-showreel{
    .text-container {
        transform: translateY(-85px) !important;
      }
  } 
  #showreel{
    padding: 130px 0;
    .showreel-script{
      width:100%;
    }
    video {
      margin: 40px 0;
    }

  }
  #contact{
    .container{
      h5.contact-text,.contact{
        transform: translateX(0);
      }
    }
  }


  
  header{
    img.logo{
      width: 60%;
    }
    .over.show{
      padding: 0;
    }
    .overlay-contact{
      .contact-email,.contact-desc{
        margin-top: 0;
      }
      ul{
        justify-content: center;
        margin: 10px;
        li.social-tiem:last-child{
          margin: 0;
        }
      }
    } 
    .overlay{
      .overlay-menu {
        display: flex;
        flex-direction: column;
        align-items: center;
        margin-right: 0;
        ul{
          li.menu-item{
            font-size: 30px;
            line-height: 30px;
            margin-bottom: 15px;
            sup{
              top: -1.25em;
              left: 5px;
            }
          }
        }
    }
      &-contact{
        margin-top: 20px;
      }
    } 
    .container.d-flex.full-height{
      flex-direction: column;
      justify-content: center;
      text-align: center;
    }
  }
  #footer{
    .footer-large-text{
      font-size: 50px;
      line-height: 50px;
      span.after-cicle{
        font-size: 100px;
      }
    }
    .footer-mini-text {
      width: 222px;
      font-size: 14px;
    }
    .footer-copyright{
      padding: 3rem 0 1rem;
      .footer-intro__left{
        justify-content: flex-start;
        figure{
          margin-bottom: 0.5rem;
        }
      }
    }
  } 
}